article.blog {
  font-family: $blog-font-family;

  h1, h2, h3 {
    font-family: $blog-font-family;
  }

  #refs {
   &:before {
      content: 'References';
      font-weight: $heading-font-weight;
      line-height: $heading-line-height;
      margin-top: $space-3;
      margin-bottom: $space-1;
      display: block;
      font-size: $h2;
    }
  }

  @if $drop-cap {
    &> p:first-child::first-letter {
      font-size: 300%;
      font-weight: $heading-font-weight;
      float: left;
      line-height: 95%;
      margin-top: 0;
      padding-right: 0.05em;
      padding-top: 0.05em;
      margin-bottom: -0.1em;
    }
  }

  .theorem, .lemma, .corollary, .definition, .proposition {
    border-left-style: solid;
    border-left-width: 3 * $border-width;
    border-left-color: $latex-environment-border-color;
    padding-left: $space-2;

    @media (prefers-color-scheme: dark) {
      border-left-color: $latex-environment-border-color-dm;
    }

  }

  h1, h2, h3, h4, h5, h6 {
    margin-top: $space-3;
  }

  .katex-display {
    overflow-x: auto;
    overflow-y: hidden;
  }

  em .katex {
    margin-left: 0.25em
  }
}